/* App.css */
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  scroll-behavior: smooth;
  background-color: #0a192f;
  color: #ccd6f6;
}

.app {
  display: flex;
  height: 100vh;
}

.sidebar {
  width: 300px;
  background-color: #0a192f;
  color: #ccd6f6;
  position: fixed;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.2);
}

.sidebar-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sidebar h1 {
  font-size: 2em;
  margin: 0;
}

.sidebar p {
  font-size: 1.2em;
  margin: 0;
}

.sidebar nav {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.sidebar-link {
  color: #64ffda;
  text-decoration: none;
  font-size: 1.2em;
  margin: 10px 0;
  transition: color 0.3s ease;
}

.sidebar-link:hover {
  color: #ccd6f6;
}

.social-icons {
  display: flex;
  gap: 20px;
  margin-top: 20px;
}

.social-icons a {
  color: #64ffda;
  font-size: 1.5em;
  transition: color 0.3s ease;
}

.social-icons a:hover {
  color: #ccd6f6;
}

.main-content {
  margin-left: 300px;
  padding: 20px;
  width: calc(100% - 300px);
  display: flex;
  flex-direction: column;
}

.section {
  padding: 80px 0;
}

.content {
  max-width: 800px;
  margin: 0 auto;
  text-align: left;
}

.section h2 {
  color: #64ffda;
  margin-bottom: 20px;
}

.section ul {
  list-style: none;
  padding: 0;
}

.section ul li {
  margin-bottom: 10px;
}

.contact .social-links a {
  margin: 0 10px;
  padding: 10px 20px;
  border: 1px solid #64ffda;
  border-radius: 5px;
  text-decoration: none;
  color: #64ffda;
}

.contact .social-links a:hover {
  background-color: #64ffda;
  color: #0a192f;
}
